* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.box {
  text-align: center;
  justify-content: center;
  height: 100vh;
}

.h11 {
  margin-bottom: 20px;
  font-size: 3rem;
  color: white;
}
.btn{
  margin-top: 20%;
  border: 2px solid black;
  background-color: white;
  opacity: 0.7;
  color: black;
  padding: 14px 28px;
  font-size: 16px;
  border-radius: 20px;
}